<template>
  <div>
    <cms-breadcrumb to="/messageSet/shopTpl/list" v-if="$route.query.mesType==2">
      <li>商家模板信息-新增</li>
    </cms-breadcrumb>
    <cms-breadcrumb to="/messageSet/userTpl/list" v-if="$route.query.mesType==1">
      <li>用户模板信息-新增</li>
    </cms-breadcrumb>
    <cms-breadcrumb to="/messageSet/platformTpl/list" v-if="$route.query.mesType==4">
      <li>平台模板信息-新增</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse="false">
        <li v-if="$route.query.mesType==2||$route.query.mesType==4">设置系统对商家发送消息的内容、方式</li>
        <li v-if="$route.query.mesType==1">设置系统对用户发送消息的内容、方式</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px">
        <el-form-item label="模板标题" prop="mesTitle" :error="errorMessage.mesTitle">
          <el-input v-model="formData.mesTitle" :autofocus="true" ref="first" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="模板标识" prop="mesCode" :error="errorMessage.mesCode">
          <el-input v-model="formData.mesCode" maxlength="100"></el-input>
          <div class="gray">建议使用英文标识，全局唯一</div>
        </el-form-item>
        <el-form-item label="模板描述" prop="remark" :error="errorMessage.remark">
          <el-input v-model="formData.remark" type="textarea" :rows="6" maxlength="200"></el-input>
        </el-form-item>
        <el-tabs v-model="activeName" @tab-click="changeTabs">
          <el-tab-pane label="站内信" name="1">          
              <el-form-item label="是否开启">
                <cms-switch v-model="formData.details[0].isOpen" @change="changeOpen"></cms-switch>
              </el-form-item>
              <el-form-item label="模板内容" prop="details[0].mesContent"
                :rules="formData.details[0].isOpen? [{ required: true, message: '模板内容必须填写', trigger: 'blur' }] :[] "
              >
                <el-input
                  v-model="formData.details[0].mesContent"
                  type="textarea"
                  :rows="4"
                  maxlength="200"
                ></el-input>
              </el-form-item>
              <el-form-item label="提示文案" prop="details[0].extendedField">
                <el-input v-model="formData.details[0].extendedField" maxlength="200"></el-input>
              </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="邮件" name="2">
            <el-form-item label="是否开启">
              <cms-switch v-model="formData.details[1].isOpen" @change="changeOpen"></cms-switch>
            </el-form-item>
            <el-form-item label="消息标题" prop="details[1].mesTitle"
             :rules="formData.details[1].isOpen? [{ required: true, message: '消息标题必须填写', trigger: 'blur' }] :[] ">
              <el-input v-model="formData.details[1].mesTitle" maxlength="100"></el-input>
            </el-form-item>
            <el-form-item label="模板内容" prop="details[1].mesContent"
             :rules="formData.details[1].isOpen? [{ required: true, message: '模板内容必须填写', trigger: 'blur' }] :[] ">
              <el-input
                type="textarea"
                :rows="4"
                v-model="formData.details[1].mesContent"
                maxlength="200"
              ></el-input>
            </el-form-item>
            <el-form-item label="提示文案" prop="details[1].extendedField">
              <el-input v-model="formData.details[1].extendedField" maxlength="200"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="手机" name="3">     
            <el-form-item label="是否开启">
              <cms-switch v-model="formData.details[2].isOpen" @change="changeOpen"></cms-switch>
            </el-form-item>
            <el-form-item label="模板id" prop="details[2].tplId"
             :rules="formData.details[2].isOpen? [{ required: true, message: '模板id必须填写', trigger: 'blur'}] :[] ">
              <el-input v-model="formData.details[2].tplId" maxlength="100"></el-input>
              <div class="gray">对应系统开启的短信服务商设置好的短信模板ID</div>
            </el-form-item>
            <el-form-item label="模板内容" prop="details[2].mesContent"
              :rules="formData.details[2].isOpen? [{ required: true, message: '模板内容必须填写', trigger: 'blur' }] :[] ">
              <el-input
                type="textarea"
                :rows="4"
                v-model="formData.details[2].mesContent"
                 maxlength="200"
              ></el-input>
            </el-form-item>
            <el-form-item label="提示文案" prop="details[2].extendedField">
              <el-input v-model="formData.details[2].extendedField" maxlength="200"></el-input>
            </el-form-item>        
          </el-tab-pane>
          <el-tab-pane label="推送" name="4" v-if="$route.query.mesType==1">
            <el-form-item label="是否开启推送">
              <cms-switch v-model="formData.details[3].isOpen" @change="changeOpen"></cms-switch>
            </el-form-item>
            <el-form-item label="标题" prop="details[3].mesTitle"
              :rules="formData.details[3].isOpen? [{ required: true, message: '标题必须填写', trigger: 'blur' }] :[] ">
              <el-input v-model="formData.details[3].mesTitle" maxlength="100"></el-input>
            </el-form-item>
            <el-form-item label="内容" prop="details[3].mesContent"
              :rules="formData.details[3].isOpen? [{ required: true, message: '内容必须填写', trigger: 'blur' }] :[] ">
              <el-input
                type="textarea"
                :rows="4"
                v-model="formData.details[3].mesContent"
                 maxlength="200"
              ></el-input>
              </el-form-item>
            <el-form-item label="提示文案" prop="details[3].extendedField">
              <el-input v-model="formData.details[3].extendedField" maxlength="200"></el-input>
            </el-form-item> 
              <el-form-item label="参数结构" prop="details[3].pushParams"
               :rules="formData.details[3].isOpen? [{ required: true, message: '参数结构必须填写', trigger: 'blur' }] :[] ">
                <el-input
                type="textarea"
                :rows="4"
                v-model="formData.details[3].pushParams"
                maxlength="500"
                ></el-input>
                <div class="gray">参数结构为json格式，最多500字符，支持动态参数占位符：${xxx},示例：{"name":${name}}</div> 
              </el-form-item>
          </el-tab-pane>
        </el-tabs>
        <div class="cms-form-footer">
          <cms-button @click="saveData(false)" v-color>保存并新增</cms-button>
          <cms-button @click="saveData(true)" v-color>保存</cms-button>
          <cms-button type="rest" @click="resetAll">重置</cms-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { messageTpSave, checkMesCode } from '@/api/setting/messageSet.js'
var defaultData = {
  mesCode: '',
  mesTitle: '',
  remark: '',
  details: [
    {
      isOpen: false,
      mesType: '1',
      mesContent: '',
      extendedField: ''
    },
    {
      isOpen: false,
      mesType: '2',
      mesContent: '',
      mesTitle: '',
      extendedField: ''
    },
    {
      isOpen: false,
      mesType: '3',
      mesContent: '',
      tplId: '',
      extendedField: ''
    },
    {
      id: '',
      isOpen: false,
      mesType: '5',
      mesContent: '',
      extendedField: '',
      pushParams: ''
    }
  ]
}
export default {
  name: 'messageSetShopSave',
  mixins: [form],
  data() {
    var validateCode = (rule, value, callback) => { // 验证模板标识的唯一性
      checkMesCode({
        mesCode: this.formData.mesCode
      }).then(res => {
        if (res.code === 200) {
          callback()
        } else {
          callback(new Error('模板标识已存在，不允许重复绑定'))
        }
      })
    }
    return {
      activeName: '1',
      formData: Object.assign({}, defaultData),
      saveRules: {// 新增规则
        mesCode: [
          rules.required('请填写模板标识'),
          rules.english(),
          rules.len(1, 150),
          rules.custom(validateCode)
        ],
        mesTitle: [rules.required('请填写模板标题'), rules.len(1, 150)],
        remark: [rules.len(1, 500)]
      }
    }
  },
  methods: {
    saveData(state) {
      this.formData.mesType = this.$route.query.mesType
      this.$refs['form'].validate((valid, failObj) => { // 验证表格
        const arr = Object.keys(failObj)// 获取验证不通过的项的 键所组成的数组
        let target = ''// 存储tabs栏下验证未通过的项
        const reg = /\d/
        if (arr.length > 0) { // 当有未通过的项时
          arr.forEach(v => {
            target = v.match(reg)// 匹配出第一项含有数字的数组
          })
        }
        if (target) { // 切换未填写的tabs
          this.activeName = String(parseInt(target[0]) + 1)// 处理到需要的字符串
        }
        if (valid && this.validateTplId()) {
          messageTpSave(this.formData).then(res => {
            if (res.code === 200) {
              this.$successMessage('保存成功')
              this.resetAll()
              if (state) {
                if (this.$route.query.mesType === 1) {
                  this.$routerLink('/messageSet/userTpl/list', 'list')
                } else if (this.$route.query.mesType === 2) {
                  this.$routerLink('/messageSet/shopTpl/list', 'list')
                } else {
                  this.$routerLink('/messageSet/platformTpl/list', 'list')
                }
              }
            } else {
              this.$errorMessage(res.message)
            }
          })
        } else {
          return false
        }
      })
    },
    validateTplId() { // 验证模板id
      const reg = /^\w+$/
      if (this.formData.details[2].isOpen) {
        if (reg.test(this.formData.details[2].tplId)) {
          return true
        } else {
          this.$errorMessage('短信模板ID格式不正确')
          return false
        }
      } else {
        return true
      }
    },
    changeOpen(status) {
      if (!status) {
        this.$refs.form.clearValidate()
      }
    },
    resetAll() {
      this.formData = Object.assign({}, defaultData)
      this.formData.details.forEach(item => {
        for (const key in item) { // 循环去清空
          item[key] = ''
          item.isOpen = false
        }
      })
      this.$refs.form.resetFields()
      this.formData.details[0].mesType = '1' // 必要的信息还是需要填回去
      this.formData.details[1].mesType = '2'
      this.formData.details[2].mesType = '3'
      this.formData.details[3].mesType = '5'
    },
    changeTabs(tab, event) {
      this.activeName = tab.name
    }
  },
  created() {
    this.resetAll()
  }
}
</script>

<style>
</style>



